.filter {
	font-size: 14px;
	padding: 20px 0px 45px 45px;
	@media (max-width:1800px) {
		position: absolute;
		right: 43px;
		top: 5px;
		background: #fff;
		border: 1px solid #1CC96C;
		box-shadow: 0 0 15px rgba(0,0,0,0.25);
		z-index: 5;
		bottom: 0;
		&.close {
			display: none;
		}
	}
	
	&_head {
		text-transform: uppercase;
		color: #1CC96C;
		font-weight: 700;
		    padding-bottom: 36px;
		@media (max-width:600px) {
			padding-bottom: 15px;	
		}
	}
	&_block {
		margin-bottom: 39px;
		@media (max-width:600px) {
			margin-bottom: 15px;
		}
		&_head {
			text-transform: uppercase;
			font-weight: 700;
			color: #FFF;
			padding-bottom: 11px;
			@media (max-width:1800px) {
				color: #323540;
			}
		}
	}
	label {
		display: block;
		cursor: pointer;
		color: rgba(255, 255, 255, 0.5);
		margin-top: 18px;
		padding-left: 20px;
		@media (max-width:1800px) {
			color: #BDBDBD;
		}
		@media (max-width:600px) {
			margin-top: 10px;
		}
	}
	input {
		&[type=checkbox] {
			display: none;
			& + span {
				display: inline-block;
				width: 18px;
				height: 18px;
				border: 2px solid #DADADA;
				vertical-align: middle;
				margin-right: 10px;
				border-radius: 2px;
				position: relative;
			}
			&:checked + span:after {
				content: "\e90c";
				font-family: 'icomoon' !important;
				  speak: none;
				  font-style: normal;
				  font-weight: normal;
				  font-variant: normal;
				  text-transform: none;
				  line-height: 1;
				  -webkit-font-smoothing: antialiased;
				  -moz-osx-font-smoothing: grayscale;
				
				position: absolute;
				top: -2px;
				left: -2px;
				font-size: 18px;
				color: #1CC96C;
				@media (max-width:1800px) {
					border-color: #1CC96C;
				}
			}
		}
	}
	&_btn {
		text-decoration: none;
		border: none;
		background: #BDBDBD;
		text-align: center;
		color: #323540;
		padding: 11px 20px;
		cursor: pointer;
		border-radius: 2px;
		&_submit {
			color: #fff;
			background: #1CC96C;
		}
		& + * {
			margin-left: 10px;
		}
	}
}