
aside {
	@include flexbox;
}
.menu {
	&_left {
		@include flexbox;
		flex-direction: column;
		justify-content: space-between;
		
		min-width: 75px;
		background: rgba(44, 46, 56, 0.9);
		padding-top: 21px;
		a {
			position: relative;
		}
		@media (max-width:1450px) {
			position: relative;
			z-index: 6;
		}
//		position: relative;
//		&:before {
//			content: '';
//			display: block;
//			position: absolute;
//			left: 0;
//			right: 0;
//			top: -10px;
//			height: 20px;
//			background: rgba(44, 46, 56, 0.5);
//		}
		[class^="icon-"], [class*=" icon-"] {
			.pl_head {
				position: absolute;
				left: 107px;
				top: 50%;
				margin: -24px 0 0 0;
				padding-top: 17px;
				@media (max-width:1450px) {
					display: none;
				}
				@media (max-height:780px) {
					display: none!important;
				}
			}
		}
		a {
			display: block;
			color: #fff;
			font-size: 49px;
			text-decoration: none;
			margin-bottom: 33px;
			text-align: center;
			@media (max-height:780px) {
				font-size: 25px;
			}
			&:hover,
			&.active {
				color: #FF2600;
			}
			&.menu_a {
				padding: 19px 0;
				margin-bottom: 0;
				&.icon-menu {
					font-size: 12px;
				}
				&.icon-search {

				}
			}
			&.icon {
				&-hot_interface {
					color: #FF2600;
				}
				&-settings {
					color: #1CC96C;
				}
			}
		}
		& div.menu_a {
			color: #fff;
			text-decoration: none;
			margin-bottom: 23px;
			text-align: center;
			padding: 15px 0;
			position: relative;
			font-size: 18px;
			background: rgba(82, 85, 101, .9);
			.left_search {
				position: absolute;
				left: 100%;
				top: 0;
				bottom: 0;
				width: 289px;
				background: rgba(82, 85, 101, .9);
				text-align: left;
				@media (max-width:1450px) {
					display: none;
				}
			}
			input {
				&[type=text] {
					background: none;
					border: none;
					outline: none;
					padding: 10px;
					margin-top: 5px;
					font-size: 14px;
					color: #fff;
					
					width: 100%;
					@include plhl {
						color: #fff;
						font-size: 14px;
						font-weight: 100;
						font-style: italic;
					}
				}
			}
		}
	}
	&_ul {
		list-style: none;
		margin: 0;
		padding: 0;
		font-size: 14px;
		padding-left: 50px;
		

		padding-right: 90px;
		margin-right: -90px;
		overflow-y: hidden;
		overflow-x: auto;
		a {
			text-decoration: none;
			
		}
		ul {
			padding-left: 22px;
		}
		li {
			position: relative;
			&.current {
				& > a {
					color: #1CC96C!important;
					font-weight: 700;
				}
			}
			&:hover {
				& > a {
					color: #1CC96C!important;
				}
			}
			& > ul {
				display: none;
			}
		}
		& > li {
			    margin-bottom: 9.5px;
			& > a {
				color: #fff;
				text-transform: uppercase;
			}
			& > ul {
				margin-top: 18px;
				    margin-bottom: 16px;
				a {
					color: rgba(255,255,255,.5);
				}
				& > li {
					margin-bottom: 7px;
					&:before {
						content: "\e911";
						font-family: 'icomoon' !important;
						  speak: none;
						  font-style: normal;
						  font-weight: normal;
						  font-variant: normal;
						  text-transform: none;
						  line-height: 1;
						  -webkit-font-smoothing: antialiased;
						  -moz-osx-font-smoothing: grayscale;
						color: rgba(255,255,255,.5);
						padding-right: 7px;
					}
					&.current {
						&:before {
							color: rgba(52,223,131,.5);
						}
					}
					& > ul {
						margin-top: 9px;
						list-style: disc;
						color: rgba(255, 255, 255, 0.5);
						padding-left: 47px;
						margin-bottom: 18px;
						li {
							margin-bottom: 7px;
							&.current {
								color: rgba(52,223,131,.5);
							}
						}
					}
				}
			}
		}
	}
	&_right {
		    padding-top: 145px;
		@media (max-height: 780px) {
			padding-top: 130px;
		}
		@media (max-width:1450px) {
			background: rgba(49, 51, 63, 0.9);
			padding-right: 20px;
			width: 300px;
		}
		@media (max-width:1450px) {
			position: absolute;
			left: -320px;
			bottom: 0;
			top: 0;
			z-index: 5;
		}
		.pl_head + div {
			overflow: hidden;
			overflow-y: auto;
			position: relative;
			bottom: 0;
			height: calc( 100% - 140px );
		}
	}
	&_upper {
		display: flex;
		justify-content: center;
		align-items: center;
		position: fixed;
		z-index: 777;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(50, 52, 64, .95);
		&_close {
			width: 62px;
			height: 62px;
			position: absolute;
			top: 20px;
			right: 20px;
			span {
				position: absolute;
				width: 60px;
				height: 7px;
				background: #1CC96C;
				
				display: block;
				top: 50%;
				left: 50%;
				margin: -3.5px 0 0 -30px;
				transform: rotate(-45deg);
				& + span {
					transform: rotate(45deg);
				}
			}
		}
		&_block {
			a {
				display: block;
				font-size: 36px;
				font-weight: 100;
				color: #fff;
				text-decoration: none;
				@media (max-width:900px) {
					font-size: 24px;
					margin: 10px 0;
				}
				@media (max-width:500px) {
					font-size: 16px;
					margin: 10px 0;
				}
				&:hover {
					color: #1CC96C;
				}
				span {
					font-size: 30px;
					padding-right: 10px;
					@media (max-width:900px) {
						font-size: 24px;
					}
					@media (max-width:500px) {
						font-size: 16px;
					}
				}
			}
		}
	}
}

.icon-menu {
	span {
		width: 18px;
		height: 2px;
		background: #fff;
		margin :0 auto 3px auto;
		display: block;
	}
}