
.main {
	height: calc( 100% - 70px );
	@include flexbox;
	justify-content: space-between;
	align-items: stretch;
	background: rgba(49,51,63,.9);
	position: relative;
	@media (max-width:1800px) {
		padding-right: 50px;	
	}
	@media (max-width:1450px) {
		padding-left: 79px;
	}
	@media (max-width:600px) {
		padding-right: 10px;
		padding-left: 10px;
	}
	.menu {
		min-width: 363px;
		@media (max-width:1450px) {
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			z-index: 4;
			min-width: 75px;
		}
		@media (max-width:600px) {
			display: none;
		}
	}
	&_content {
		width: 100%;
		padding: 24px 0 0 27px;
		@media (max-width:600px) {
			padding-left: 0;	
		}
	}
	.filter {
		min-width: 380px;
		overflow-y: auto;
		margin-right: 20px;
		margin-bottom: 40px;
		    margin-top: 32px;
		
		@media (max-width:600px) {
			margin: 0;
			top: -65px;
			left: 10px;
			right: 10px;
			bottom: 14px;
			padding: 20px;
			    min-width: 0;
		}
	}
	
	
	&_content {
		&_top {
			@include flexbox;
			justify-content: space-between;
			padding-bottom: 25px;
			align-items: flex-end;
			@media (max-width:780px) {
				display: block;
				    padding-bottom: 10px;
			}
		}
	}
}
section {
	background: #F3F4F6;
	border-radius: 3px;
	width: 100%;
	height: calc( 100% - 100px );
	    padding: 26px 77px 30px 30px;
	position: relative;
	@media (max-width:600px) {
			
	}
	@media (max-width:600px) {
		padding: 10px 10px 30px 10px;
		    height: calc( 100% - 60px);
	}
	&.card {
		padding-right: 35px;
		.btn_right_sidebar {
			background: #FF2600;
			&:after {
				border-left-color: #FF2600;
			}
		}
	}
}
.brands {
	display: flex;
	justify-content: space-between;
	align-items: center;
	
	height: 95px;
	
	&_head {
		text-align: center;
		font-size: 18px;
		color: #34DF83;
		font-weight: 100;
	}
	&.owl-carousel {
		@include flexbox;
		justify-content: space-between;
		align-items: center;
		&.owl-loaded {
			padding-top: 25px;
			@media (max-width:600px) {
				padding-top: 10px;
			}
		}
	}
	@media (max-width:600px) {
		padding-top: 10px;
    	height: 50px;
	}
	img {
		width: auto!important;
		margin: 0 auto;
	}
	
}
.bread {
	color: #969696;
	font-size: 14px;
	font-weight: 700;
	text-transform: uppercase;
	a {
		color: #969696;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
	* {
		display: inline-block;
		vertical-align: middle;
		&[class^="icon-"], [class*=" icon-"] {
			color: #34DF83;
			font-size: 9px;
			margin: 0 3px 0 6px;
			display: inline-block;
			vertical-align: middle;
		}
	}
}
.sort {
	font-size: 12px;
	color: #999999;
	* {
		display: inline-block;
		vertical-align: bottom;
	}
	a {
		font-size: 16px;
		margin: 0 4px 0 6px;
		text-decoration: none;
		color: #999999;
		@include transition;
		&:hover,
		&.current {
			color: #1CC96C;
		}
	}
}

@media (max-width:1450px) {
	.open {
		.menu_right {
			left: 75px;
		}
		.menu_left div.menu_a .left_search,
		.left_search {
			display: block;
		}
		[class^="icon-"], [class*=" icon-"] {
			.pl_head {
				display: block!important;
			}
		}
		&_menu {
			.menu_left {
				[class^="icon-"], [class*=" icon-"] {
					.pl_head {
						display: none!important;
					}
				}
			}
		}
	}
}
	.open {
		&_menu {
			.menu_left {
				[class^="icon-"], [class*=" icon-"] {
					.pl_head {
						display: none!important;
					}
				}
			}
		}
	}
