header {
	background: rgba(44, 46, 56, 1);
	font-size: 12px;
	color: #fff;
	@include flexbox;
	
	justify-content: space-between;
	align-items: stretch;
	
	padding: 15px;
	box-shadow: 0 0 16px rgba(0,0,0,.5);
	& > div {
		@include flexbox;
		align-items: center;
		&:last-child {
			align-items: stretch;
		}
	}
	a {
		text-transform: uppercase;
		font-weight: 700;
		color: #fff;
		text-decoration: none;
	}
	.logo {
		    margin-right: 55px;
		@media (max-width:600px) {
			margin-right: 0;
		}
	}
	.icon-menu {
		display: none;
		@media (max-width:600px) {
			display: block;
			    padding-top: 17px;
		}
	}
}
nav {
	@media (max-width:680px) {
		display: none;
	}
	a {
		margin: 0 33.2px;
		@include transition;
		@media (max-width:800px) {
			margin: 0 12px;
		}
		&:hover {
			color: #FF2600;
		}
	}
}
.cabinet {
	font-size: 23px;
	a {
		position: relative;
		margin: 0 14px; 
		@include transition;
		&:hover {
			color: #FF2600;
			span {
				color: #fff;
				border-color: rgba(255,255,255,.4);
			}
		}
	}
	span {
		font-size: 12px;
		font-weight: 100;
		position: absolute;
		top: -6px;
		right: 0px;
		transform: translate(50%,-50%);
		padding: 0px 4px;
		background: #FF2600;
		white-space: nowrap;
		border-radius: 10px;
		border: 1px solid #FF2600;
		@include transition;
		color: #fff;
		font-family: 'Roboto', sans-serif;
	}
}
.date {

	
	
	height: 100%;
    border-left: 1px solid #FF2600;
    margin-left: 46px;
    padding-left: 30px;

	@include flexbox;
    align-items: center;
    justify-content: center;
    color: #FF2600;
    text-transform: uppercase;
    font-weight: 700;
    padding-right: 7px;
	span {
		padding: 0 14px;
	}
	@media (max-width:1085px) {
		display: none;
	}
}