.card {
	background: #fff;
	.card {
		height: calc( 100% - 35px);
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		padding-top: 16px;
    	border-top: 4px solid #F2F2F2;
	}
	&_left {
		overflow-x: auto;
		height: 100%;
		padding-right: 7px;
		min-width: 315px;
		@media (max-width:1200px) {
			display: none;
		}
		&_top {
			padding: 12px 10px;
			background: #F2F2F2;
			color: #3E3E3E;
			margin-top: 9px;
			a {
				color: #969696;
				text-decoration: none;
				margin-right: 5px;
				font-size: 18px;
				vertical-align: middle;
				margin-top: -4px;
				display: inline-block;
			}
		}
		.catalog_item {
			margin: 9px 0 0 0;;
		}
		& .catalog_item_left,
		& .item_dis {
			width: 100%;
		}
	}
	&_rigth {
		width: 100%;
		display: flex;
    	flex-direction: column;
		margin-left: 11px;
		padding-left: 14px;
		border-left: 4px solid #F2F2F2;
		@media (max-width:1200px) {
			border-left: none;
			margin-left: 0;
			padding-left: 0;
		}
	}
	&_top {
		display: flex;
		justify-content: space-between;
		padding-bottom: 20px;
		min-height: 255px;
		&_btn {
			display: block;
			width: 100%;
			padding: 11px 9px;
			border: 1px solid rgba(255,255,255,0);
			text-decoration: none;
			color: #3E3E3E;
			font-size: 16px;
			&.active {
				border-color: #1CC96C;
			}
			span {
				color: #969696;
				margin-right: 12px;
				font-size: 14px;
				vertical-align: middle;
			}
			
		}
		&_right {
			width: 100%;
			    margin-left: 20px;
		}
		&_img {
			border: 1px solid #DEDEDE;
		}
	}
	&_bottom {
		overflow-y: auto;
		margin-right: -25px;
    	padding-right: 25px;
		
		.card_top_btn {
			background:#1CC96C;
			color: #fff;
			span {
				color: #fff;
			}
		}
		&_item {
			& > div:not(.card_top_btn) {
				padding: 28px 40px;
				color: #2E303A;
				font-size: 14px;
				@media (max-width:600px) {
					padding: 14px;
				}
			}
		}
	}
}
.characteristics {
	& > div {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 11px;
		&:nth-child(2n +1) {
			background: rgba(242,242,242,.5);
		}
	}
	
}
.calc {
	&_top {
		text-align: right;
		.btn  {
			display: inline-block;
		}
		padding-bottom: 35px;
		& + .calc_block {
			.calc_block_item > div:not(.item_add_input) {
				text-align: center;
				& + div {
					border-left: 2px solid #545669;
					text-align: center;
				}
				&.sale {
					background: #F2F2F2;
					border-color: #F2F2F2;
					color: #FF2600;
				}
			}
		}
	}
	&_block {
		&_item {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			margin-bottom: 7px;
			& > * {
				width: 50%;
			}
			& > div:not(.item_add_input) {
				padding: 15px 0;
				color: #fff;
				font-size: 16px;
				text-transform: uppercase;
				text-align: right;
				padding-right: 4px;
				border: 2px solid #545669;
				
				border-right: none;
				& + div {
					text-align: left;
					padding-left: 4px;
					border-left: none;
					border-right: 2px solid #545669;
				}
//				&.sale {
//					background: #F2F2F2;
//					border-color: #F2F2F2;
//					color: #FF2600;
//				}
			}
			.item_add_input {
				margin-right: 7px;
				margin-bottom: 0;
			}
			.btn {
				padding: 10px 10px;
				white-space: nowrap;
				width: 49%;
				span {
					margin-left: 5px;
//					    font-size: 18px;
				}
				&:not(.btn_without) {
					span {
						font-size: 18px;
					}
					
				}
			}
			
		}
		textarea {
			border: 2px solid #545669;
			min-height: 155px;
			padding: 14px;
			background: none;
			display: block;
			width: 100%;
			font-size: 14px;
			color: #fff;
			@include plhl {
				color: rgba(255,255,255,.5);
				font-size: 14px;
			}
			margin-bottom: 10px;
		}
		button {
			background: #BDBDBD;
			padding: 15px;
			color: #000;
			font-size: 14px;
			border-radius: 3px;
		}
		.item_add_input {
			border-radius: 3px;
			margin-bottom: 7px;
				background: #BDBDBD;
				a {
					padding: 8px 0;
					color: #323540;
				}
				input {
					background: #BDBDBD;
				}
			}
	}
	
}
