.catalog {
	@include flexbox;
	justify-content: center;
	flex-wrap: wrap;
	align-items: flex-start;
	
	overflow-y: auto;
	height: calc( 100% - 90px );
	    margin-right: -76px;
    margin-left: 12px;
    padding-right: 43px;
	@media (max-width:600px) {
		margin-right: 0;
		padding-right: 0;
		margin-left: 0;
	}
	&_item {
		flex-basis: calc( 50% - 14px );
		background: #FFF;
		
		padding: 18px 18px 18px 29px;
		
		margin: 0 7px 15px 7px;
		
		@media (max-width:950px) {
			flex-basis: calc( 100% - 14px );
		}
		@media (max-width:620px) {
			    padding: 10px 10px 7px 10px;
		}
		@media (max-width:600px) {
			flex-basis:100%;
		}
		
		&_top {
			@include flexbox;
			justify-content: space-between;
			align-items: flex-start;
			padding-bottom: 18px;
			a {
				display: block;
			}
		}
		&_center {
			@include flexbox;
			justify-content: space-between;
			@media (max-width:1200px) {
				display: block;
			}
		}
		&_left {
			@include flexbox;
			justify-content: space-between;
			align-items: flex-start;
			@media (max-width:400px) {
				display: block;
				padding-left: 20px;
			}
		}
		&_right {
			min-width: 100px;
			@media (max-width:1200px) {
				@include flexbox;
				justify-content: space-between;
				margin-left: -7px;
				margin-right: -7px;
				padding-top: 7px;
				& > * {
					width: 25%;
					margin: 0 5px;
					&:last-child {
						width: 50%;
						display: flex;
						justify-content: space-between;
						align-items: self-start;
						& > * {
							width: 50%;
							& + * {
								margin-left: 5px;
							}
						}
					}
				}
			}
		}
	}
}
.item {
	border: 1px solid #DEDEDE;
	border-left: 1px solid #1CC96C;
	&:hover {
		box-shadow: inset 4px 0px 0px 0px rgba(28, 201, 108, 0.29), 0 0 4px rgba(0,0,0,.25);
		.item_price:not(.item_price_new) {
			background: none;
		}
	}
	&.item_red {
		border-color: #FFCDC4;
		border-left: 1px solid #FF2600;
		&:hover {
			box-shadow: inset 4px 0px 0px 0px rgba(255, 38, 0, 0.29), 0 0 4px rgba(0,0,0,.25);
		}
	}
	a {
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
	&_name {
		font-size: 16px;
		color: #3E3E3E;
		padding-bottom: 1px;
	}
	&_cat {
		font-size: 12px;
		text-transform: uppercase;
		color: #999999;
	}
	&_img {
		min-width: 90px;
		width: 90px;
		height: 90px;
		border: 1px solid #DEDEDE;
		margin-right: 19px;
		img {
			width: 100%;
		}
	}
	&_dis {
		font-size: 12px;
		font-weight: 100;
		li {
			list-style: disc;
			margin-left:  7px;
			margin-bottom: 5px;
		}
	}
	&_price {
		background: #F2F2F2;
		font-size: 14px;
		color: #4F4F4F;
		border-radius: 3px;
		display: block;
		    padding: 11px 0;
		text-align: center;
		margin-bottom: 5px;
		&_new {
			font-weight: 700;
			color: #FF2600;
		}
	}
	&_add {
		&_input {
			@include flexbox;
			justify-content: space-between;
			align-items: center;
			
			border-radius: 3px 3px 0 0;
			border: 1px solid #CACACA;

			text-align: center;
			@media (max-width:1200px) {
				border-radius: 3px;
				height: 39px;
			}
			& + div:not(.calc_block_item) {
				@include flexbox;
				justify-content: space-between;
				align-items: center;
				a {
					background: #CACACA;
					width: 50%;
					padding: 5px 0;
					color: #88898E;
					text-align: center;
					text-decoration: none!important;
					font-size: 19px;
					@media (max-width:1200px) {
						height: 39px;
    					padding-top: 9px;
					}
					&:first-child {
						border-radius: 0 0 0 3px;
						@media (max-width:1200px) {
							border-radius: 3px 0 0 3px;
						}
					}
					&:last-child {
						border-radius: 0 0 3px 0;
						background: #E6E6E6;
						@media (max-width:1200px) {
							border-radius: 0 3px 3px 0;
						}
					}
				}
			}
			[class^="icon-"], 
			[class*=" icon-"] {
				width: 50%;
				text-align: center;
			}
			input {
				border: none;
				outline: none;
				max-width: 50px;
				width: 60%;
				text-align: center;
				font-size: 14px;
			}
			a {
				text-decoration: none;
				color: #BDBDBD;
				font-size: 19px;
				display: inline-block;
				vertical-align: middle;
				padding: 5px 0;
				width: 20%;
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
}

.pager {
	text-align: center;
	padding: 20px 0;
	a {
		display: inline-block;
		border: 1px solid rgba(255,255,255,0);
		vertical-align: middle;
		font-size: 16px;
		text-align: center;
		color: #3E3E3E;
		border-radius: 2px;
		width: 32px;
		height: 32px;
		text-decoration: none;
		padding-top: 5px;
		margin: 0 4px;
		&:hover,
		&.current {
			
			color: #fff;
			background: #D7D7D7;
			border-color: #D7D7D7;
		}
	}
}
