@import "variables";
@import "mixins";

html,
body {
	font-family: 'Roboto', sans-serif;
	height: 100%;
	min-height: 100%;
	background:  url(../images/bg.png) no-repeat top center;
	overflow: hidden;
} 
.green {
	color: #34DF83!important;
}
.btn { 
	background: #1CC96C;
	padding: 10px 15px;
	border-radius: 2px;
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	transition: all ease 0.4s;
	&_white {
		background: #fff;
		border: 2px solid rgba(189,189,189,.5);
	}
	&_without {
		background: none!important;
		&:hover {
			border-color: #fff;
			background: #fff!important;
			color: #000;
			span {
				transition: all ease 0.4s;
				color: #FF2600;
			}
		}
	}
}
input {
	&[type=checkbox],
	&[type=radio] {
		display: none!important; 
	}
	&[type=radio] {
		& + span {
			width: 20px;
			height: 20px;
			border: 2px solid rgba(0,0,0,.24);
			border-radius: 50%;
			display: inline-block;
			margin-left: 2px;
			vertical-align: middle;
			margin-right: 27px;
			margin-top: -3px;
		}
		&:checked {
			& + span {
				border-color: #1CC96C; 
				position: relative;
				&:after {
					content: '';
					display: block;
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background: #1CC96C;
					position: absolute;
					top: 3px;
					left: 3px;
				}
			}
		}
	}
}
.center {
	text-align: center;
}