
.btn {
	&_right_sidebar {
		text-decoration: none;
		color: #fff;
		font-size: 18px;
		background: #1CC96C;
		width: 60px;
		height: 40px;
		position: absolute;
		top: 17px;
    	right: -18px;
		text-align: center;
		vertical-align: middle;
		padding-top: 11px;
		z-index: 50;
		@include transition;
		@media (max-width:1800px) {
			transform: scale(-1,1);
		}
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			width: 0;
			height: 0;
			border: 20px solid rgba(255,255,255,0);
			border-left-color: #1CC96C;
			border-left-width: 9px;
			top: 0;
			right: -29px;
		}
	}
}

.pl_head {
	border: 2px solid #545669;
	border-left: none;
//	border-top-width: 2.5px;
	margin-left: 30px;
	font-size: 14px;
	color: #fff;
	padding: 15px 0;
	text-align: center;
	margin-bottom: 60px;
	position: relative;
	text-transform: uppercase;
	height: 51px;
	font-weight: 700;
	    width: 258px;
	font-family: 'Roboto', sans-serif;
	&:before {
		content: '';
		display: block;
		position: absolute;
		width: 20px;
		height: 50px;
		left: -19px;
		top: -1px;
		background: url(../images/pl.png) no-repeat 0 0;
	}
}
.info {
	color: #2E303A;
		font-size: 14px;
	p {
		margin-bottom: 20px;
		
	}
}
