.home {
	width: 100%;
	.menu {
		min-width: 75px;
	}
	&_wrapper {
		max-width: 1140px;
		padding: 0 15px;
	}
	&_slider {
		font-size: 36px;
		font-weight: 100;
		color: #fff;
		max-width: 660px;
		margin: 100px auto 95px auto;
		
		&_name {
			font-size: 18px;
			color: rgba(255,255,255,0.5);
			text-align: right;
		}
	}
	&_menu {
		display: flex;
		justify-content: flex-start;
		align-items: stretch;
		padding-bottom: 70px;
		&_item {
			max-width: 255px;
			padding-right: 60px;
			&:not(.second) {
				a {
					text-transform: uppercase;
					
					font-size: 14px;
					color: #fff;
					&:last-child {
						color: #FF2600;
					}
					&.active,
					&:hover {
						color: #34DF83;
					}
				}
			}
			&.second {
				a {
					&:hover {
						text-decoration: underline;
					}
				}
			}
			a {
				display: block;
				color: rgba(255,255,255,0.5);
				text-decoration: none;
			}
			& + * {
				border-left: 1px solid #9A9BA0;
				padding-left: 70px;
				max-width: 100%;

			}
		}
	}
	&_nav {
		display: flex;
		justify-content: space-between;
		align-items: center;
		
		&_item {
			width: 25%;
			padding-bottom: 25px;
			border-bottom: 5px solid rgba(0,0,0,0);
			text-decoration: none;
			&.active {
				border-color: #34DF83;
			}
			[class^="icon-"], [class*=" icon-"] {
				font-size: 52px;
				display: block;
				text-align: center;
				color: #fff;
			}
			&_name {
				font-size: 18px;
				text-transform: uppercase;
				font-weight: 100;
				display: block;
				color: #fff;
				text-align: center;
//				margin-bottom: 25px;
    			margin-top: 24px;
			}
		}
	}
	
}
