.popup {
//	max-width: 390px;
	&_test {
		//REMOVE
		    display: flex;
			justify-content: center;
			align-items: center;
			position: fixed;
			z-index: 777;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background: rgba(50, 52, 64, 0.95);
		//REMOVE
	}
	&_head {
		background: #1CC96C;
		color: #fff;
		font-size: 24px;
		width: 100%;
		padding: 21px 22px 0 0;
		border-radius: 2px 2px 0 0;
		box-shadow: 0 0 4px rgba(0,0,0,.14), 0 4px 4px rgba(0,0,0,.3);
		    position: relative;
		i {
			font-style: normal;
			font-family: 'Roboto', sans-serif;
			padding-left: 35px;
		}
		span {
			padding-bottom: 17px;
			padding-left: 24px;
			display: block;
		}
	}
	&_content {
		background: #fff;
		border-radius: 0 0 2px 2px;
		padding: 37px 24px 26px 24px;
		box-shadow:  0 0 24px rgba(0,0,0,0.24), 0 24px 24px rgba(0,0,0,0.30);
		font-size: 16px;
		&.authorization {
			min-width: 400px;
		}
		&.registration {
			min-width: 700px;
			max-width: 700px;
		}
		p {
			color: rgba(0,0,0,.54);
			margin-bottom: 30px;
		}
		&_block {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			& > * {
				width: 48%;
				
			}
		}
	}
	&_bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&_tabs {
		a {
			display: inline-block;
			padding: 10px 25px;
			color: rgba(255,255,255,0.75);
			font-size: 14px;
			font-weight: 500;
			text-decoration: none;
			border-bottom: 1px solid rgba(0,0,0,0);
			
			&.active {
				color: #fff;
				border-color: #fff;
			}
		}
	}
	&_reg {
		width: 100%;
		overflow: hidden;
		&_in {
			width: 300%;
			display: flex;
			align-items: stretch;
			transition: all ease 0.4s;
		}
		&_item {
			min-width: 33.3333%;
			width: 33.3333%;
		}
	}
}
.input {
	min-height: 51px;
	&_row {
		font-size: 16px;
		&_full {
			min-width: 100%;
		}
		input {
			border: none;
			border-bottom: 2px solid #1CC96C;
			width: 100%;
			display: block;
			line-height: 24px;
			color: rgba(0,0,0,.87);
//			margin-bottom: 25px;
		}
	}
	&_head {
		font-size: 12px;
		color: rgba(0,0,0,.38);
	}
	&_bottom {
		    font-size: 12px;
			color: rgba(0, 0, 0, 0.38);
			text-align: right;
			height: 0;
			margin-top: -15px;
			margin-bottom: 15px;
	}
	&_restore {
		font-size: 12px;
		text-align: right;
		padding-top: 37px;
//		margin-bottom: 28px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		& + * {
			margin-top: 28px;
		}
		a {
			margin-left: 15px;
			text-decoration: none;
			&:hover {
				text-decoration: underline;
			}
		}
	}
}
.link {
	text-transform: uppercase;
	
	font-size: 14px;
	font-weight: 500;
	text-decoration: none;
	color: rgba(0,0,0,.54);
	
}
.label {
	&_checkbox {
		margin-top: 12px;
		margin-bottom: 22px;
		cursor: pointer;
		font-size: 16px;
		color: rgba(0,0,0,.84);
	}
}

.captcha {
	text-align: center;
	padding: 70px 0;
}
